import React from 'react';
import {Route} from 'react-router-dom';
import '../assets/css/Home.css';
import Cell from "../components/cell/Cell";
import Swiper from "../components/swiper/Swiper";
import {connect} from 'react-redux';
import {update} from '../store/actions';
import * as types from "../store/type";


class Home extends React.Component{
    // state={
    //     list:[]
    // }

    //访问mock接口去请求数据
    constructor(props){
        // console.log(313);
        super();
        props.getHome();
        props.getBanner();
        // React.axios(
        //     {
        //         url:'/mock/home',
        //         params:{
        //             _page:1,
        //             _limit:12
        //         }
        //     }).then(
        //         res=>{
        //             // console.log(res);
        //             this.setState({
        //                 list:res.data.data
        //             })
        //         }
        // );
    }


    render() {
        let {banner,home} = this.props;
        // console.log(home,banner);
        // console.log(this.state.list);
        return (
            <div className={'home'}>
                <Swiper data={banner}/>
                {/*{*/}
                {/*    this.state.list.map(item=>(*/}
                {/*<Route list={home} dataName={'home'} component={Cell}/>*/}
                    <Cell list={home} dataName={'home'}/>
                {/*    ))*/}
                {/*}*/}
            </div>
        )
    }
}
const initMapStateToProps = state => ({
    banner:state.banner,
    home:state.home
});

const initMapDispatchToProps = dispatch => ({
    getHome:() => {
        dispatch({type:'CLEAR_HOME'});
        dispatch(update({
            api:'home',
            type:types.UPDATE_HOME,
            _page:3,
            _limit:9
        }))
    },
    getBanner:()=>{
        // console.log(111)
        dispatch(update({
            api:'home',
            _page:2,
            _limit:4,
            type:types.UPDATE_BANNER
        }))
    }
});


export default connect(initMapStateToProps,initMapDispatchToProps)(Home)