import React from 'react'
import {NavBar, Icon, List, SwipeAction,Stepper} from 'antd-mobile'
import '../assets/css/Cart.css'
import {update} from "../store/actions";
import store from '../plugins/redux';
import * as types from "../store/type";
import {connect} from "react-redux";
class ShopCart extends React.Component{
    state={
        checkAll:false,
        cart:'',
        totalPrice:0
    }

    render(){
        let {cart}=this.props;
        // console.log(cart);
        return (
            <div className={'shopCart'}>
                <div className="cart" >
                    <header className="wy-header">
                        <div className="wy-header-icon-back" onClick={()=>{this.props.history.go(-1)}}><span></span></div>
                        <div className="wy-header-title">购物车</div>
                    </header>
                    <div className="aui-car-coupons">
                        {/*{*/}
                        {/*    cart.length>0?*/}
                        {/*        <div style={{display: "flex"}}>*/}
                        {/*            <i style={{fontSize:16,color:"red"}} onClick={()=>{this.props.history.push('/home')}}>继续选购商品 </i>*/}
                        {/*            <button type="warning" size={"small"} style={{marginTop:10,marginLeft:10}} onClick={this.removeAll}>全部删除</button>*/}
                        {/*        </div>:*/}
                        {/*        <div style={{display: "flex"}}>*/}
                        {/*            <i>购物车没有商品!!! </i>*/}
                        {/*            <button type="primary" size={"small"} style={{marginTop:10,marginLeft:10}} onClick={()=>{this.props.history.push('/home')}}>挑选商品</button>*/}
                        {/*        </div>*/}

                        {/*}*/}

                    </div>
                    <div className="weui-content">
                    <div className="weui-panel weui-panel_access">
                        {
                            cart && cart.map((item,idx)=>{
                                return (
                                    <div key={item._id}>
                                        <div className="weui-panel__hd"><span>{item.cat_id}旗舰店</span><a href="#" className="wy-dele"></a></div>
                                        <div className="shops">
                                            <div className="float">
                                                <input className='ipt' type="checkbox"  name="cartpro"
                                                       checked={this.props.cart[idx].check} onChange={() => {this.checkOne(idx,item._id)} }/>
                                                <div className="imags"><a href="#"><img className="imag" src={item.goods_small_logo} alt=""/></a></div>
                                                <div className="box">
                                                    <h1 className="box-shop"><a href="#" className="name">商品名称</a></h1>
                                                    <p className={'pt3'}>规格：<span>{item.evaluate}</span></p>
                                                    <div className="price">¥<em>{item.goods_price}</em></div>
                                                </div>
                                                <div className="btn-group" role="group">
                                                    <button type="button" className="btn"  onClick={()=>{this.change({item:item,num:-1})}}>-</button>
                                                    <button type="text" ref={'input'} className="btn">{item.cart_number}</button>
                                                    <button type="button" className="btn" onClick={()=>{this.change({item:item,num:+1,_id:item._id})}}>+</button>
                                                </div>
                                                <div className="delete"><input type="button" value="删除" onClick={()=>{this.delete({item:item})}}/></div>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })
                        }
                </div>
            </div>
        <div className="goIndex"><button onClick={()=>{this.props.history.push('/home')}}>回到首页</button></div>
        <div className="clearCart"><button onClick={()=>{this.getClear()}}>清空购物车</button></div>

        <div className="footer"></div>
        <div className="footer-box" >
            <div className={'box'}>
                <input className="ipt" type="checkbox"  name={"checkAll"} checked={this.state.checkAll} onChange={this.allCheck}/>
            </div>
            <span style={{padding:10}}>全选</span>
            <div className="npd">
                <p className="pt">合计：<i>￥</i><em style={{marginLeft:5,color:'#e07100'}} >{this.state.totalPrice}</em></p>
            </div>
            <a href="#" >
                <p className="pt2" onClick={this.payment}>结算</p>
            </a>
            </div>
                {/*<div v-show='false'>{{mapCar}}</div>*/}
            </div>
        </div>
        )

    }

    payment = () => {
        if(this.state.cart){
            alert("用户，您所选的物品的总金额为：" + this.state.totalPrice + "确定要结算吗？");
            this.state.cart.map(item=>{
                if(item.check === true) {
                    this.props.getDelete({item:item});
                    this.state.totalPrice = 0;
                }
            });
        }else {
            alert('您还没有勾选商品');
        }
        this.state.checkAll = false;
    }

    change = (value)=>{
        let input = this.refs.input;
        // console.log(input.innerHTML);
        this.props.getChange(value);

        this.props.cart.map((item,idx)=>{
            if(item._id == value._id){
                input.innerHTML = this.props.cart[idx].cart_number;
            }
        });
        this.setState({
            cart:this.props.cart
        });
        // console.log(this.props.cart.number);
        this.forceUpdate(()=>{
            // this.setState({num:this.state.num+1});
            this.totalAll(this.state.cart);
        });
    }

    delete = (value) =>{
        this.props.getDelete(value);
        this.state.totalPrice = 0;
    }

    //单选
    checkOne = (index,_id) => {
        // console.log(index,_id);
        let {cart} = this.props;
        // console.log(typeof(cart[index].check));
        // if(typeof(cart[index].check) == 'undefined'){
        //     console.log(cart[index].check);
        //     cart[index].check = 'true';
        // }
        cart[index].check = !cart[index].check;
        // console.log(cart[index].check)
        //            判断全部商品是否被勾选上
        let checkAll = true;
        for (let i = 0; i < cart.length; i++) {
            //     只要有一个没选中就为false
            if (!cart[i].check) {
                checkAll = false;
                break;
            }
        }
        // console.log(cart);
        this.setState({
            cart,
            checkAll
        });
        this.forceUpdate(()=>{
            this.totalAll(this.state.cart);
        });
    }

    //总价
    totalAll = (itm) => {
        // console.log(itm);
        let totalPrice = 0;
        // let cart = [...this.state.cart];
        for (let i = 0; i < itm.length; i++) {
            if (itm[i].check) {
                totalPrice += itm[i].goods_price * itm[i].cart_number;
            }
        }
        this.setState({
            totalPrice: totalPrice
        });
        store.dispatch({ type: 'TOTAL_PRICE', payload: totalPrice });
    }

    getClear = ()=>{
        this.props.getClearl();
        this.state.totalPrice = 0;
    }


    //全选
    allCheck = (ev) => {
        this.setState({
            [ev.target.name]: ev.target.checked
        });
        // console.log(ev.target);
        this.forceUpdate(()=>{

        });
        let cart = [...this.state.cart];
        let checkAll = !this.state.checkAll;
        // console.log(checkAll);

        for (let i = 0; i < cart.length; i++) {
            cart[i].check = checkAll;
        }
        this.setState({
            cart,checkAll
        })
        this.forceUpdate(()=>{
            this.totalAll(this.state.cart);
        });
    }

}

const initMapStateToProps = state=>{
    // console.log(state.cart);
    return {
        cart:state.cart
    }
};
const initMapDispatchToProps = dispatch=>({
    getChange:(item)=>{
        // console.log(item);
        dispatch({type:types.UPDATE_CART,payload:item})
    },
    getDelete:(item)=>{
        // console.log(item);
        dispatch({type:types.REMOVE_CART,payload:item})
    },
    getClearl:()=> {
        // console.log('13');
        dispatch({type: types.CLEAR_CART});
    }
});

export default connect(initMapStateToProps,initMapDispatchToProps)(ShopCart)