import React from 'react';

import '../assets/css/Reg.css';
import {checkUser} from "../store/actions";
import * as types from "../store/type";
import {connect} from "react-redux";

export default class Reg extends React.Component{
    state={
        username:'',
        password:'',
        errMess:''
    }
    // componentDidMount(){
    //     let formData = new FormData();
    //     formData.append('username',this.username)
    //     formData.append('password',this.password)
    //     let input = document.getElementById('icon')
    //     formData.append('icon',input.files[0])
    //
    //     // React.axios({
    //     //     url:'/api/reg',
    //     //     method:'post',
    //     //     data: formData
    //     // }).then(
    //     //     res=>{
    //     //         console.log(res);
    //     //         res.data.err===0?this.props.history.push('/login'):this.state.errMess=res.data.msg
    //     //     }
    //     // )
    // }
    selectIcon=()=>{
        let input = document.getElementById('icon');
        input.click();
    }

    reg = () => {
             // console.log(this.refs.icon.files[0])
             let formData = new FormData();
             let file = this.refs.icon;
             // console.log(this.state.username,this.state.password,file);
             formData.append('username',this.state.username);
             formData.append('password',this.state.password);
             // console.log(formData);
             formData.append('icon',file.files[0]);
             React.axios({
                url:'/api/reg',
                method:'post',
                data:formData
             }).then(
                res=>{
                    if (res.data.err===1){
                        this.setState({mess:res.data.msg})
                    } else {
                        alert('注册成功');
                        this.props.history.push('/login')
                    }
             })
    }

    changeIpt = (ev) => {
        this.setState({
            [ev.target.name] : ev.target.value
        })
    }

    render() {
        return (
            <div className={'reg'}>
                <p className="fhbtn"><a onClick={()=>this.props.history.go(-1)}></a></p>
                <div className="form--heading" onClick={()=>{this.selectIcon()}}>Welcome! Sign Up</div>
                <input ref={'icon'} type="file" id="icon" style={{display:'none'}}/>
                <form>
                    <input type="text" placeholder="Name" name={'username'}
                           value={this.state.username} className={'name'} onChange={this.changeIpt}/>
                    <input type="password" placeholder="Password" name={'password'}
                           value={this.state.password} className={'name'} onChange={this.changeIpt}/>
                    {/*<div className="file">*/}
                    {/*    <p>{this.state.errMess}</p>*/}
                    {/*</div>*/}
                    <input type={'button'} className="button" onClick={this.reg} value={'Sign Up'}/>
                </form>
            </div>
        )
    }
}


