import React from 'react';
import '../assets/css/Detail.css';
import xing from '../assets/img/xing.png'
import querystring from 'query-string';

import date from "../utils/date";
import {update} from "../store/actions";
import * as types from "../store/type";
import {connect} from "react-redux";

class Detail extends React.Component{

    state={
        detail:{}
    };

    goback=()=>{
        this.props.history.go(-1);
    };

    componentDidMount(){
        // console.log(this.props);
        let _id=this.props.match.params.id;
        let dataName=querystring.parse(this.props.location.search).dataName;
        this.props.getDetail(dataName,_id);
    }

    render(){

        let {detail} = this.props;
        // console.log(detail);
        let mockStyleStr = '<span style="background:deepskyblue">介绍:</span>';

        let el=null;


        if (detail){
            el = (
                <div className="detail">
                    <div className="nav">
                        <div className={'ul'}>
                            <b className="l-btn" onClick={this.goback}></b>
                        </div>
                    </div>
                    <div className="content">
                        <div className="header clear"><h2><img src={detail.goods_small_logo} alt=""/></h2><p>{detail.cat_id}</p></div>
                        <div className="cont">
                            <h3>{detail.goods_name}</h3>
                            <div className="time"><p>{date(detail.upd_time)}<span><img src={require("../assets/img/zan.png")} alt=""/></span></p>
                            </div>
                            <div
                                className="text-box"
                                dangerouslySetInnerHTML={{__html:mockStyleStr + detail.goods_introduce}}
                            >
                            </div>
                        </div>
                        <div className={'btn'}>
                            <button className={'button'} onClick={()=>{this.addCart(detail)}}>加入购物车</button>
                            <button className={'button'} onClick={()=>{this.props.history.push('/cart')}}>立即购买</button>
                        </div>
                    </div>
                    <div style={{height:50}}></div>
                    <div className="foot-btn">
                        <ul>
                            <li className="say"><a href="#">
                                <i></i><span>0</span>
                            </a></li>
                            <li className="zan"><a href="#">
                                <i></i><span>0</span>
                            </a></li>
                            <li className="xing"><a href="#">
                                <i><img src={xing} alt=""/></i>
                            </a></li>
                            <li className="fx"><a href="#">
                                <i><img src={require('../assets/img/fx.png')} alt=""/></i>
                            </a></li>
                        </ul>
                    </div>
                </div>
            )
        } else {
            el = <div>骨架屏</div>
        }

        return el;

    }

    addCart = (item)=> {
        // console.log(item)
        this.props.getCart(item);
     }
}



const initMapStateToProps=state=>({
    detail:state.detail
});

const initMapDispatchToProps=dispatch=>({
    getDetail:(api,_id)=>dispatch(update({
        api,
        type:types.UPDATE_DETAIL,
        _id
    })),
    getCart:(item)=>{
        // console.log(item);
        dispatch({type:types.ADD_CART,payload:item})
    }
});

export default connect(initMapStateToProps,initMapDispatchToProps)(Detail)