import React from 'react';

import style from '../assets/css/User.module.css';
import {connect} from "react-redux";
import store from '../plugins/redux';
import * as types from '../store/type';

class User extends React.Component{
    state={}

    logout = () => {
        // console.log(1111);
        React.axios({
            url:'/api/logout',
            method:'delete'
        }).then(
            res => {
                // console.log(res);
                if(res.data.err === 0){
                //    后端已经将设置的后端cookie删除，则之后应该通知前端，将设置的cookie进行删除操作
                    window.localStorage.removeItem('store_user');
                //    在将状态管理里面的数据进行删除
                    store.dispatch({type:types.CLEAR_USER});
                }
            }
        )
    }

    render() {
        let {user} = this.props;
        // console.log(user);
        return (
            <div className={style.user}>
                <div className={style.img}>
                    <img src={React.baseUrl + user.data.icon}/>
                    <span className={style.span}>{user.data.nikename}</span>
                </div>
                <div className={style.ul}>
                    <ul>
                        <li>
                            <span>{user.data.follow}</span>
                            <p>关注</p>
                        </li>
                        <li>
                            <span>{user.data.fans}</span>
                            <p>粉丝</p>
                        </li>
                    </ul>
                </div>
                <div className={style.all}>
                    <div className={style.transaction}>
                        <img src={require('../assets/img/center-icon-jyjl.png')} className={style['center-list-icon']}/>
                        <p className={style.txt}>交易记录</p>
                        <span className={style.arrow}>&gt;</span>
                    </div>
                    <div className={style.transaction}>
                        <img src={require('../assets/img/center-icon-yhk.png')} className={style['center-list-icon']}/>
                        <p className={style.txt}>我的银行卡</p>
                        <span className={style.arrow}>&gt;</span>
                    </div>
                    <div className={style.transaction}>
                        <img src={require('../assets/img/center-icon-sc.png')} className={style['center-list-icon']}/>
                        <p className={style.txt}>我的收藏</p>
                        <span className={style.arrow}>&gt;</span>
                    </div>
                    <div className={style.transaction}>
                        <img src={require('../assets/img/center-icon-dz.png')} className={style['center-list-icon']}/>
                        <p className={style.txt}>地址管理</p>
                        <span className={style.arrow}>&gt;</span>
                    </div>
                    <div className={style.transaction}>
                        <img src={require('../assets/img/center-icon-dlmm.png')} className={style['center-list-icon']}/>
                        <p className={style.txt}>密码修改 </p>
                        <span className={style.arrow}>&gt;</span>
                    </div>
                    <div className={style.transaction}>
                        <img src={require('../assets/img/center-icon-out.png')} className={style['center-list-icon']}/>
                        <p className={style.txt} onClick={this.logout}>退出账号</p>
                        <span className={style.arrow}>&gt;</span>
                    </div>
                </div>
            </div>
        )
    }
}

const initMapStateToProps = state => ({
    user:state.user
});


export default connect(initMapStateToProps,null)(User)