import React from 'react';
import {Route,NavLink} from "react-router-dom";

import '../assets/css/Footer.css';

export default class Footer extends React.Component{
    state={}
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div >
                <div className="foot-black"></div>
                <div className="weui-tabbar wy-foot-menu" style={{height:'48'}}>
                    <NavLink activeClassName="weui-tabbar__item weui-bar__item--on" className="weui-tabbar__item"
                             to={'/home'} >
                        <div className="weui-tabbar__icon foot-menu-home"></div>
                        <p className="weui-tabbar__label">首页</p>
                    </NavLink>
                    <NavLink activeClassName="weui-tabbar__item weui-bar__item--on" className="weui-tabbar__item"
                                 to={'/classify'}>
                        <div className="weui-tabbar__icon foot-menu-list"></div>
                        <p className="weui-tabbar__label">分类</p>
                    </NavLink>
                    <NavLink activeClassName="weui-tabbar__item weui-bar__item--on" className="weui-tabbar__item"
                             to={'/cart'}>
                        {/*<span className="weui-badge" style="position: absolute;top: -.4em;right: 1em;"></span>*/}
                        <div className="weui-tabbar__icon foot-menu-cart"></div>
                        <p className="weui-tabbar__label">购物车</p>
                    </NavLink>
                    <NavLink activeClassName="weui-tabbar__item weui-bar__item--on" className="weui-tabbar__item"
                             to={'/user'}>
                        <div className="weui-tabbar__icon foot-menu-member"></div>
                        <p className="weui-tabbar__label">我的</p>
                    </NavLink>
                </div>
            </div>
        );
    }
}






