import React from 'react';

import $ from 'jquery';
import '../assets/css/Login.css';
import {checkUser} from "../store/actions";
import {connect} from "react-redux";
import store from "../plugins/redux";

export default class Login extends React.Component{
    state={
        username:'',
        password:'',
        mess:'',
    }

    changeIpt = (ev) => {
        // console.log(ev.target.value);
        this.setState({
            [ev.target.name] : ev.target.value
        })
        // console.log(this.state);
    }

    login = () => {
        let newState = {...this.state};
        // console.log(newState);
        store.dispatch(checkUser({
            api:'login',
            method:'post',
            username:this.state.username,
            password:this.state.password
        })).then(
            data=>{
                if (data.err===1){
                    this.setState({mess:data.msg})
                } else {
                    alert("登陆成功");
                    window.localStorage.setItem('store_user',JSON.stringify(data));
                    this.props.history.push('/user');
                }

            }
        )
    }

    render() {
        // console.log(this.state.mess)
        return (
            <div className={'login'}>
                <p className="fhbtn"><a onClick={()=>window.history.go(-1)}></a></p>
                <div className="form--head">Welcome back! </div>
                <form>
                    <input type="text" placeholder="Name" className={'name'} name={'username'}
                        onChange={this.changeIpt}/>
                    <input type="password" placeholder="Password" className={'name'} name={'password'}
                        onChange={this.changeIpt}/>
                    <p className={'p'}>{this.state.mess}</p>
                    <input type={'button'} className="button" onClick={this.login} value={"Login"}/>
                    <input type={'button'} className="button bttn" value={'SignUp'} onClick={() => {this.props.history.push('/reg')}}/>
                </form>
            </div>
        )
    }
}
