import React from 'react';
import ReactDom from 'react-dom';
import App from "./layouts/App";
import 'antd-mobile/dist/antd-mobile.css';
import {BrowserRouter,Route} from "react-router-dom";
//在主模块中引入axios
import './plugins/axios';
import store from './plugins/redux';
import {Provider} from 'react-redux';


 React.baseUrl = 'http://localhost:3333';
//取localStorage同步状态管理
let local = window.localStorage.getItem('store_user');
if (local){
    store.dispatch({type:'CHECK_USER',payload:JSON.parse(local)})
}

let cart = window.localStorage.getItem('shopCart');
if (cart){
    let shop = JSON.parse(cart);
    shop.map(item=>{
      item.is_select = '';
    });
    store.dispatch({type:'CHECK_CART',payload:shop});
}

ReactDom.render(
    <Provider store={store}>
        <BrowserRouter>
            <Route component={App}/>
        </BrowserRouter>
    </Provider>
    ,
    document.getElementById('root'));





