import React from 'react';
import {Route,Redirect,Switch} from "react-router-dom";
import User from "../pages/User";
import Footer from "./Footer";
import Home from '../pages/Home';
import Classify from '../pages/Classify';
import ShopCart from '../pages/Cart';
import Loading from '../components/loading/Loading';
import Reg from '../pages/Reg';
import Login from '../pages/Login';



import ErrorPage from "../pages/ErrorPage";
import Detail from "../pages/Detail";
import Header from "./Header";
import Auth from "../guard/Auth";
import * as types from "../store/type";
import {connect} from "react-redux";


class App extends React.Component{
    state={}

    //判断bLoading是否在数据请求回来之前是否显现
    //利用订阅发布模式：发布是在拦截器中进行发布的；而且还必须是订阅在前，发布在后
    // constructor(props){
    //     super(props);
    //     pubsub.subscribe('UPDATE_LOADING',(mess,data) =>{
    //
    //         this.setState({bLoading:data});
    //     });
    // }

    //在加载之前就要进行判断，判断所加载的页面是否需要loading，header和footer
    static getDerivedStateFromProps(nextProps,nextState){
    //    获取pathname
    //     console.log(nextProps);
    //     let path = nextProps.location.pathname;
        //解构
        let {viewNav,viewFoot,location:{pathname}} = nextProps;
        // console.log(viewNav,viewFoot)
        //利用正则进行匹配
        if(/home|classify/.test(pathname)){
            // console.log(1111);
            viewNav(true);
            viewFoot(true);
        }
        if(/user|ErrorPage/.test(pathname)){
            viewNav(false);
            viewFoot(true);
        }if(/login|reg|detail|cart/.test(pathname)){
            viewNav(false);
            viewFoot(false);
        }
        return null;
    }

    render() {
        let {bLoading,bNav,bFooter} = this.props;
        // console.log(bFooter);
        return (
            <div className={'app'}>

                {/* eslint-disable-next-line react/jsx-no-undef */}
                {bLoading && <Loading/>}

                {bNav && <Header/>}
                <Switch>
                    <Route path={'/home'} component={Home}></Route>
                    <Auth path={'/user'} component={User}></Auth>
                    <Route path={'/reg'} component={Reg}></Route>
                    <Route path={'/classify'} component={Classify}></Route>
                    <Route path={'/cart'} component={ShopCart}></Route>
                    <Route path={'/login'} component={Login}></Route>
                    <Route path={'/detail/:id'} component={Detail}></Route>
                    <Redirect exact from={'/'} to={'/home'}/>
                    <Route component={ErrorPage}/>
                </Switch>
                {bFooter && <Footer/>}
            </div>
        );
    }
}

const initMapStateToProps = state => {
    return {
        bNav:state.bNav,
        bFooter:state.bFooter,
        bLoading:state.bLoading
    }
};

const initMapDispatchToProps = dispatch => {
    return {
    viewNav:(bl)=>{
        // console.log(bl);
        dispatch({type:types.VIEW_NAV,payload:bl})
    },
    viewFoot:(bl)=>{
        // console.log(bl);
        dispatch({type:types.VIEW_FOOT,payload:bl})
    }
}}


export default connect(initMapStateToProps,initMapDispatchToProps)(App)


