//异步请求
//写一个函数用于来发送请求（详情页以及某个页面）
import axios from 'axios';
//用户介权
import {CHECK_REG, CHECK_USER} from "./type";

//用dispath来处理不同的业务（业务的先后，同步以及异步的区别）
const update = ({api,_page=1,_limit=10,type,_id=null}) => {
    return (
        (dispatch) => {

            // console.log(222,api)//api:"column"
            //    发送axios请求
            return axios({
                //如果有id的存在，就证明请求的详情页的数据，反之，则是某页面的数据
                url: _id ? `/api/${api}/${_id}` : `/api/${api}`,
                params: {_page, _limit}
            }).then(
                //得到的数据将给reducer去处理业务以及更改数据仓库的数据
                res => {
                    // console.log(333,res.data.data);
                    dispatch({type, payload: res.data.data})
                }
            ).catch(
                //    捕捉错误
                err => {
                    // console.log('cuowu');
                    new Error(err);
                }
            )
        }
    );
}
    //简写
    // const update = ({api,_page=1,_limit=10,type,id=null}) => dispatch => (
    //     axios({
    //         url: id ? `/mock/${api}/${id}` : `/mock/${api}`,
    //         params: {_page,_limit}
    //     }).then(
    //         res=>{
    //             dispatch({type,payload:res.data.data});
    //         }
    //     ).catch(
    //         err=>new Error(err)
    //     )
    // );

    const checkUser = ({api,method='get',username,password,icon})=>{
    // console.log(api,newState);
    return dispatch =>  axios({
            url:`/api/${api}`,
            method,
            // params: method === 'get' ? {username,password} : null,
            data: icon ? {username,password,icon} :  {username,password},
        }).then(
            res=> {
                // console.log(res.data.data);
                // newState && dispatch({type:CHECK_USER,payload:res.data});
                // data && dispatch({type:CHECK_REG,payload:res.data});
                dispatch({type:CHECK_USER,payload:res.data});
                return res.data;
            }
        ).catch(
            err => new Error(err)
        )
    }



export {update,checkUser}