import * as types from './type';

export default (state,{type,payload}) => {
//    负责突变state和获取state 要求一定要返回state
//    运用switch...case的语句   需要拷贝更新并返回
    switch (type) {
        case types.VIEW_NAV:
            return {...state,bNav:payload};
        case types.VIEW_FOOT:
            let newState = {...state};
            newState.bFooter = payload;
            return newState;
        case types.VIEW_LOADING:
            //Object.assign()浅拷贝
            return Object.assign({},state,{bLoading:payload});
        case types.UPDATE_HOME:
            return {...state,home:payload};
        case types.CLEAR_HOME:
            return {...state,home:[]};
        case types.UPDATE_BANNER:
            return {...state,banner:payload};
        case types.UPDATE_CLASSIFY:
            return {...state,classify:payload};
        case types.UPDATE_DETAIL:
            // console.log(payload)
            return {...state,detail:payload[0]};
        case types.CHECK_USER:
            return {...state,user:payload};
        case types.CHECK_REG:
            return {...state,reg:payload};
        case types.CHECK_CART:
            return {...state,cart:payload};
        case types.CLEAR_USER:
            return {...state,user:{
                err:1,msg:"未登录",data:{}
                }};
        case types.ADD_CART:
            //需要在这里判断用户是否登录：登录：就判断数组中是否有该商品，有：数量加一；无：直接将该商品放入数组
            //拷贝
            // console.log(payload);
            let arr = [...state.cart];
            // console.log(arr);
            arr.map(item=>{
                if(!item.check){
                    let item1 = {...item,check:''};
                    item = item1;
                }else{
                    item.check = '';
                }
            });
            let find = false;
            if(state.user.err === 0){
                // console.log(find)
                arr.forEach(item=>{
                    // console.log('232');
                    if(item._id == payload._id){
                        item.cart_number++;
                        find = true;
                    }
                });
                if(!find){
                    // console.log('232');
                    payload.cart_number = 1;
                    payload.check = '';
                    arr.push(payload);
                }
            }
            console.log(arr[arr.length-1].check);
            window.localStorage.setItem('shopCart',JSON.stringify(arr));
            // console.log(arr);
            return {...state,cart:arr};
        case types.UPDATE_CART:
            // console.log(payload.num);
            let shop = [...state.cart];
            shop.forEach(item=>{
                if(item._id === payload.item._id){
                    if(payload.num == -1 && item.cart_number == 1){
                        return;
                    }
                    item.cart_number += payload.num;
                    // console.log(item.number);
                }
            });
            window.localStorage.setItem('shopCart',JSON.stringify(shop));
            return {...state,cart:shop}
        case types.REMOVE_CART:
            let shopCar = [...state.cart];
            shopCar.forEach((item,idx)=>{
                if(item._id === payload.item._id){
                    shopCar.splice(idx,1);
                }
            });
            window.localStorage.setItem('shopCart',JSON.stringify(shopCar));
            return {...state,cart:shopCar};
        case types.TOTAL_PRICE:
            return { ...state, totalPrice: payload };
        case types.CLEAR_PRICE:
            return { ...state, totalPrice: 0 };
        case types.CLEAR_CART:
            window.localStorage.setItem('shopCart',[]);
            return { ...state, cart: [] };
        default:
            return state;

    }
}