import React from 'react';
import {connect} from 'react-redux';
import {update} from "../store/actions";
import * as types from "../store/type";
import {Link,NavLink} from 'react-router-dom';
import '../assets/css/Classify.css'


class Classify extends React.Component{
    state={
        classify:[]
    }

    constructor(props){
        super();
        props.getClassify();
        // React.axios({
        //     url:'/mock/column',
        //     params:{
        //         _page:2,
        //         _limit: 6
        //     }
        // }).then(
        //     res=>this.setState({
        //         classify:res.data.data
        //     })
        // )
    }
    render() {
        let {classify} = this.props;
        // console.log(classify);
        return (
            <div className="classify">
                <div className="wy-content">
                    <aside>
                        <div className="menu-left scrollbar-none" id="sidebar">
                            <ul>
                                <li className={'active'}><a>酒水食品</a></li>
                                <li><a href={'#'}>家用电器</a></li>
                                <li><a href={'#'}>电脑办公</a></li>
                                <li><a href={'#'}>家具家居</a></li>
                                <li><a href={'#'}>服装服饰</a></li>
                                <li><a href={'#'}>个化护装</a></li>
                                <li><a href={'#'}>衣帽鞋包</a></li>
                                <li><a href={'#'}>运动户外</a></li>
                                <li><a href={'#'}>汽车用品</a></li>
                                <li><a href={'#'}>母婴玩具</a></li>
                                <li><a href={'#'}>医药保健</a></li>
                                <li><a href={'#'}>图书音像</a></li>
                                <li><a href={'#'}>旅游生活</a></li>
                                {/*<li><NavLink activeClassName={'active'} to={''}>酒水食品</NavLink></li>*/}
                                {/*<li><NavLink activeClassName={'active'} to={''}>家用电器</NavLink></li>*/}
                                {/*<li><NavLink activeClassName={'active'} to={''}>电脑办公</NavLink></li>*/}
                                {/*<li><NavLink activeClassName={'active'} to={''}>家具家居</NavLink></li>*/}
                                {/*<li><NavLink activeClassName={'active'} to={''}>服装服饰</NavLink></li>*/}
                                {/*<li><NavLink activeClassName={'active'} to={''}>个化护装</NavLink></li>*/}
                                {/*<li><NavLink activeClassName={'active'} to={''}>衣帽鞋包</NavLink></li>*/}
                                {/*<li><NavLink activeClassName={'active'} to={''}>运动户外</NavLink></li>*/}
                                {/*<li><NavLink activeClassName={'active'} to={''}>汽车用品</NavLink></li>*/}
                                {/*<li><NavLink activeClassName={'active'} to={''}>母婴玩具</NavLink></li>*/}
                                {/*<li><NavLink activeClassName={'active'} to={''}>医药保健</NavLink></li>*/}
                                {/*<li><NavLink activeClassName={'active'} to={''}>图书音像</NavLink></li>*/}
                                {/*<li><NavLink activeClassName={'active'} to={''}>旅游生活</NavLink></li>*/}
                            </ul>
                        </div>
                    </aside>
                    <section className="menu-right padding-all j-content">
                        <h5>酒水食品</h5>
                        <ul className={'tt'}>
                            {
                                classify && classify.map(item=>(
                                    <li className="w-3" key={item._id}>
                                        <a href="#"></a>
                                        <Link to={`/detail/${item._id}?dataName=home`}>
                                            <img src={item.goods_small_logo} />
                                            <span>{item.cat_two_id}</span>
                                        </Link>
                                    </li>
                                ))
                            }
                        </ul>
                    </section>
                </div>
            </div>
        )
    }


}

const initMapStateToProps = state => {
    // console.log(state.column)
    return ({
        classify:state.classify
    })
};

const initMapDispatchToProps = dispatch => ({
    getClassify:()=>{
        dispatch(update({
            api:'home',
            _page:1,
            _limit:30,
            type:types.UPDATE_CLASSIFY
        }))
    }
});


export default connect(initMapStateToProps,initMapDispatchToProps)(Classify)